// src/taskpane/taskpane.js
// Taskpane logic: provider selection + keys/models + shared max tokens.
//
// Requirements implemented:
// - Default provider selection only in panel (not in formulas)
// - Two tabs: Gemini and OpenAI
// - Single max output tokens setting shared by both providers
// - No temperature/top-p controls

import {
  PROVIDERS,
  DEFAULTS,
  storageBackendName,
  getDefaultProvider,
  setDefaultProvider,
  getMaxOutputTokens,
  setMaxOutputTokens,
  getOnboardingSeen,
  setOnboardingSeen,
  getApiKey,
  setApiKey,
  clearApiKey,
  getModel,
  setModel,
  getGeminiReasoningEffort,
  setGeminiReasoningEffort,
  getOpenAIReasoningEffort,
  setOpenAIReasoningEffort,
  getRequestLog,
  clearRequestLog,
  getPersistentCacheStats
} from "../shared/core";

import { aiMinimalTest, clearAllCaches } from "../shared/providers";

function el(id) {
  return document.getElementById(id);
}

function setStatus(line, detailsObj) {
  el("statusLine").textContent = line || "";
  if (detailsObj) {
    try {
      el("statusDetails").textContent = JSON.stringify(detailsObj, null, 2);
    } catch {
      el("statusDetails").textContent = String(detailsObj);
    }
  } else {
    el("statusDetails").textContent = "";
  }
}

function formatNumber(value) {
  const n = Number(value);
  if (!Number.isFinite(n)) return "0";
  return Math.round(n).toLocaleString("en-US");
}

const CURRENCY_SYMBOL = "";
const COST_ICON = "$";
const REASON_ICON = "\uD83E\uDDE0";
const TOKEN_IN_ICON = "IN";
const TOKEN_REASON_ICON = "REF";
const TOKEN_OUT_ICON = "OUT";

const FAVORITE_ICON = "\u2605";

function formatCost(value) {
  const n = Number(value);
  if (!Number.isFinite(n)) return `${CURRENCY_SYMBOL}0.0000`;
  return `${CURRENCY_SYMBOL}${n.toFixed(4)}`;
}

function formatDuration(ms) {
  const n = Number(ms);
  if (!Number.isFinite(n)) return "0.00s";
  return `${(n / 1000).toFixed(2)}s`;
}

function formatTime(ts) {
  try {
    const d = new Date(ts);
    return d.toLocaleTimeString("fr-FR", { hour: "2-digit", minute: "2-digit" });
  } catch {
    return "--:--";
  }
}

function formatBytes(bytes) {
  const n = Number(bytes);
  if (!Number.isFinite(n) || n <= 0) return "0 KB";
  if (n < 1024) return `${n} B`;
  if (n < 1024 * 1024) return `${(n / 1024).toFixed(1)} KB`;
  return `${(n / (1024 * 1024)).toFixed(2)} MB`;
}

const TOKEN_EXP_MIN = 7;
const TOKEN_EXP_MAX = 17;
const UNKNOWN_MODEL_LABEL = "modele inconnu";
const GEMINI_REASONING_LEVELS = ["auto", "minimal", "low", "medium", "high"];
const OPENAI_REASONING_LEVELS = ["none", "minimal", "low", "medium", "high"];
const HELP_SELECTOR = "[data-help]";

let keyState = { gemini: "", openai: "" };

const DEFAULT_MODELS = {
  gemini: DEFAULTS.geminiModel,
  openai: DEFAULTS.openaiModel
};

const MODEL_CATALOG = {
  gemini: [
    {
      id: "gemini-3-pro-preview",
      label: "Gemini 3 Pro Preview",
      prices: { input: 2, output: 12, cachedInput: 0.2 },
      costLevel: 3,
      reasoningLevel: 3
    },
    {
      id: "gemini-3-flash-preview",
      label: "Gemini 3 Flash Preview",
      prices: { input: 0.5, output: 3, cachedInput: 0.05 },
      costLevel: 2,
      reasoningLevel: 2,
      recommended: true
    },
    {
      id: "gemini-2.5-flash-lite",
      label: "Gemini 2.5 Flash Lite",
      prices: { input: 0.1, output: 0.4, cachedInput: 0.01 },
      costLevel: 1,
      reasoningLevel: 1
    }
  ],
  openai: [
    {
      id: "gpt-5.2",
      label: "GPT-5.2",
      prices: { input: 1.75, output: 14, cachedInput: 0.175 },
      costLevel: 3,
      reasoningLevel: 3,
      supportsReasoningNone: true
    },
    {
      id: "gpt-5-mini",
      label: "GPT-5 Mini",
      prices: { input: 0.25, output: 2, cachedInput: 0.025 },
      costLevel: 2,
      reasoningLevel: 2,
      recommended: true,
      supportsReasoningNone: false
    },
    {
      id: "gpt-5-nano",
      label: "GPT-5 Nano",
      prices: { input: 0.05, output: 0.4, cachedInput: 0.005 },
      costLevel: 1,
      reasoningLevel: 1,
      supportsReasoningNone: false
    }
  ]
};

const MODEL_LOOKUP = {
  gemini: new Map(MODEL_CATALOG.gemini.map((m) => [m.id.toLowerCase(), m])),
  openai: new Map(MODEL_CATALOG.openai.map((m) => [m.id.toLowerCase(), m]))
};

const PROVIDER_LABELS = {
  gemini: "Gemini",
  openai: "OpenAI"
};

function repeatIcon(icon, level) {
  const count = Math.max(1, Number(level) || 1);
  return new Array(count + 1).join(icon);
}

function getModelMeta(provider, model) {
  const p = String(provider || "").toLowerCase();
  const key = String(model || "").trim().toLowerCase();
  const map = MODEL_LOOKUP[p];
  if (map && key && map.has(key)) return map.get(key);
  const fallback = DEFAULT_MODELS[p];
  if (map && fallback && map.has(fallback.toLowerCase())) return map.get(fallback.toLowerCase());
  return null;
}

function openaiModelAllowsNone(model) {
  const key = String(model || "").trim().toLowerCase();
  const meta = MODEL_LOOKUP.openai?.get(key);
  if (meta && typeof meta.supportsReasoningNone === "boolean") return meta.supportsReasoningNone;
  return true;
}

function getGeminiAllowedReasoningLevels(modelId) {
  const key = String(modelId || "").trim().toLowerCase();
  if (key.includes("gemini-3") && key.includes("flash")) {
    return ["auto", "minimal", "low", "medium", "high"];
  }
  if (key.includes("gemini-3")) {
    return ["auto", "low", "high"];
  }
  if (key.includes("gemini-2.5")) {
    return ["auto", "minimal", "low", "medium", "high"];
  }
  return ["auto", "low", "high"];
}

function coerceReasoningLabel(label, allowed, levels) {
  if (!label) return allowed[0] || "";
  if (allowed.includes(label)) return label;
  const idx = levels.indexOf(label);
  if (idx < 0) return allowed[0] || "";
  let best = allowed[0] || "";
  let bestDist = Number.POSITIVE_INFINITY;
  for (const option of allowed) {
    const optIdx = levels.indexOf(option);
    if (optIdx < 0) continue;
    const dist = Math.abs(optIdx - idx);
    if (dist < bestDist) {
      bestDist = dist;
      best = option;
    }
  }
  return best;
}

function syncGeminiReasoningEffort(modelId, preferred) {
  const effortEl = el("geminiReasoningEffort");
  if (!effortEl) return "";
  const allowed = getGeminiAllowedReasoningLevels(modelId);
  let label = getReasoningEffortLabel(preferred, GEMINI_REASONING_LEVELS);
  if (!label) label = GEMINI_REASONING_LEVELS[0];
  if (!allowed.includes(label)) {
    if (label === "minimal") label = "low";
    if (label === "medium") label = "high";
  }
  label = coerceReasoningLabel(label, allowed, GEMINI_REASONING_LEVELS);
  const index = GEMINI_REASONING_LEVELS.indexOf(label);
  effortEl.value = String(index >= 0 ? index : 0);
  effortEl.setAttribute("aria-valuetext", label);
  return label;
}

function getReasoningEffortLabel(value, levels = OPENAI_REASONING_LEVELS) {
  const raw = String(value || "").trim().toLowerCase();
  if (!raw) return "";
  const idx = Number(raw);
  if (Number.isFinite(idx)) {
    const clamped = Math.min(levels.length - 1, Math.max(0, Math.round(idx)));
    return levels[clamped] || "";
  }
  return raw;
}

function syncOpenAIReasoningEffort(modelId, preferred) {
  const effortEl = el("openaiReasoningEffort");
  if (!effortEl) return "";
  const allowNone = openaiModelAllowsNone(modelId);
  const wrap = effortEl.closest(".reasoning");
  if (wrap) wrap.classList.toggle("reasoning--no-none", !allowNone);
  effortEl.dataset.allowNone = allowNone ? "1" : "0";

  const allowed = allowNone ? OPENAI_REASONING_LEVELS : OPENAI_REASONING_LEVELS.slice(1);
  let next = getReasoningEffortLabel(preferred);
  if (!allowed.includes(next)) {
    next = allowed[0] || "minimal";
  }
  const index = OPENAI_REASONING_LEVELS.indexOf(next);
  effortEl.value = String(index >= 0 ? index : 0);
  effortEl.setAttribute("aria-valuetext", next);
  return next;
}

function formatRate(value) {
  const n = Number(value);
  if (!Number.isFinite(n)) return `${CURRENCY_SYMBOL}0`;
  const decimals = n >= 1 ? 2 : 3;
  let text = n.toFixed(decimals);
  text = text.replace(/\.?0+$/, "");
  return `${CURRENCY_SYMBOL}${text}`;
}

function modelIndicators(meta) {
  return {
    cost: repeatIcon(COST_ICON, meta.costLevel),
    reasoning: repeatIcon(REASON_ICON, meta.reasoningLevel)
  };
}

function modelOptionLabel(meta) {
  const indicators = modelIndicators(meta);
  const favorite = meta.recommended ? `${FAVORITE_ICON} ` : "";
  return `${favorite}${meta.id} | ${indicators.cost} | ${indicators.reasoning}`;
}

function estimateCost(entry) {
  if (!entry) return 0;
  const provider = String(entry.provider || "").toLowerCase();
  const meta = getModelMeta(provider, entry.model);
  if (!meta) return 0;

  const inputTokens = Math.max(0, Number(entry.inputTokens || 0));
  const outputTokens = Math.max(0, Number(entry.outputTokens || 0));
  const reasoningTokens = Math.max(0, Number(entry.reasoningTokens || 0));
  const cachedInputTokens = Math.max(0, Number(entry.cachedInputTokens || 0));
  const billableOutputTokens = outputTokens + reasoningTokens;

  const cached = Math.min(cachedInputTokens, inputTokens);
  const uncached = Math.max(0, inputTokens - cached);

  const prices = meta.prices || {};
  const inputRate = Number(prices.input || 0);
  const cachedRate = Number(prices.cachedInput ?? prices.input ?? 0);
  const outputRate = Number(prices.output || 0);

  return (uncached / 1e6) * inputRate + (cached / 1e6) * cachedRate + (billableOutputTokens / 1e6) * outputRate;
}

function normalizeUsageEntry(entry) {
  return {
    provider: entry?.provider || "",
    inputTokens: Number(entry?.inputTokens || 0),
    outputTokens: Number(entry?.outputTokens || 0),
    reasoningTokens: Number(entry?.reasoningTokens || 0),
    cachedInputTokens: Number(entry?.cachedInputTokens || 0),
    totalTokens: Number(entry?.totalTokens || 0),
    ok: !!entry?.ok,
    cache: !!entry?.cache,
    ms: Number(entry?.ms || 0),
    fn: entry?.fn || "AI",
    model: entry?.model || "",
    ts: Number(entry?.ts || 0),
    message: entry?.message || "",
    formula: entry?.formula || ""
  };
}

function populateModelSelect(provider, selectEl, currentModel) {
  if (!selectEl) return;
  const list = MODEL_CATALOG[provider] || [];
  const requested = String(currentModel || "").trim();
  const requestedKey = requested.toLowerCase();
  const fallback = DEFAULT_MODELS[provider] || "";

  selectEl.innerHTML = "";
  let matched = false;

  for (const meta of list) {
    const option = document.createElement("option");
    option.value = meta.id;
    option.textContent = modelOptionLabel(meta);
    if (meta.id.toLowerCase() === requestedKey) {
      option.selected = true;
      matched = true;
    }
    selectEl.appendChild(option);
  }

  if (requested && !matched) {
    const option = document.createElement("option");
    option.value = requested;
    option.textContent = `${requested} | hors liste`;
    option.selected = true;
    selectEl.appendChild(option);
    return;
  }

  if (!requested && fallback) {
    selectEl.value = fallback;
  }
}

function renderPricingTable() {
  const gemBody = el("pricingTableBodyGemini");
  const oaiBody = el("pricingTableBodyOpenAI");
  if (!gemBody && !oaiBody) return;
  if (gemBody) gemBody.innerHTML = "";
  if (oaiBody) oaiBody.innerHTML = "";

  const renderProvider = (provider, body) => {
    if (!body) return;
    const list = MODEL_CATALOG[provider] || [];
    for (const meta of list) {
      const row = document.createElement("tr");
      const indicators = modelIndicators(meta);

      const cellModel = document.createElement("td");
      cellModel.textContent = meta.id;

      const cellInput = document.createElement("td");
      cellInput.className = "pricing-table__col-price";
      cellInput.textContent = formatRate(meta.prices?.input);

      const cellOutput = document.createElement("td");
      cellOutput.className = "pricing-table__col-price";
      cellOutput.textContent = formatRate(meta.prices?.output);

      const cellCost = document.createElement("td");
      cellCost.className = "pricing-table__col-icon";
      cellCost.textContent = indicators.cost;

      const cellReason = document.createElement("td");
      cellReason.className = "pricing-table__col-icon";
      cellReason.textContent = indicators.reasoning;

      row.appendChild(cellModel);
      row.appendChild(cellInput);
      row.appendChild(cellOutput);
      row.appendChild(cellCost);
      row.appendChild(cellReason);
      body.appendChild(row);
    }
  };

  renderProvider(PROVIDERS.GEMINI, gemBody);
  renderProvider(PROVIDERS.OPENAI, oaiBody);
}

function setModalOpen(id, isOpen) {
  const modal = el(id);
  if (!modal) return;
  modal.classList.toggle("is-open", isOpen);
  modal.setAttribute("aria-hidden", isOpen ? "false" : "true");
}

function isModalOpen(id) {
  const modal = el(id);
  return !!modal && modal.classList.contains("is-open");
}

function setPricingModalOpen(isOpen) {
  setModalOpen("pricingModal", isOpen);
}

function setOnboardingModalOpen(isOpen) {
  setModalOpen("onboardingModal", isOpen);
}

function setOnboardingKeysModalOpen(isOpen) {
  setModalOpen("onboardingKeysModal", isOpen);
}

function openPricingModal() {
  renderPricingTable();
  setPricingModalOpen(true);
}

function closePricingModal() {
  setPricingModalOpen(false);
}

function setKeyStatus(id, hasKey) {
  const node = el(id);
  if (!node) return;
  const ok = !!hasKey;
  node.textContent = ok ? "Cl enregistre" : "Cl manquante";
  node.classList.toggle("key-status--ok", ok);
  node.classList.toggle("key-status--missing", !ok);
}

function updateKeyAlerts() {
  const missingAll = !keyState.gemini && !keyState.openai;
  document.querySelectorAll("[data-key-alert]").forEach((node) => {
    node.classList.toggle("is-hidden", !missingAll);
  });
}

function updateKeyPlaceholders() {
  const geminiInput = el("geminiKey");
  const openaiInput = el("openaiKey");
  if (geminiInput) {
    geminiInput.placeholder = keyState.gemini ? "Cl enregistre (laisser vide pour conserver)" : "AIza...";
  }
  if (openaiInput) {
    openaiInput.placeholder = keyState.openai ? "Cl enregistre (laisser vide pour conserver)" : "sk-...";
  }
}

function setKeyState(geminiKey, openaiKey) {
  keyState = {
    gemini: (geminiKey || "").trim(),
    openai: (openaiKey || "").trim()
  };
  updateKeyPlaceholders();
  updateKeyAlerts();
}

function clampExp(value) {
  const n = Number(value);
  if (!Number.isFinite(n)) return TOKEN_EXP_MIN;
  return Math.min(TOKEN_EXP_MAX, Math.max(TOKEN_EXP_MIN, Math.round(n)));
}

function expToTokens(exp) {
  return 2 ** clampExp(exp);
}

function tokensToExp(tokens) {
  const n = Number(tokens);
  if (!Number.isFinite(n) || n <= 0) return TOKEN_EXP_MIN;
  return clampExp(Math.log2(n));
}

function setProviderButtons(provider) {
  const isGemini = provider === PROVIDERS.GEMINI;
  el("providerGemini").classList.toggle("segmented__btn--active", isGemini);
  el("providerOpenAI").classList.toggle("segmented__btn--active", !isGemini);
  el("providerGemini").setAttribute("aria-pressed", isGemini ? "true" : "false");
  el("providerOpenAI").setAttribute("aria-pressed", !isGemini ? "true" : "false");
}

function getSelectedProvider() {
  const geminiActive = el("providerGemini").classList.contains("segmented__btn--active");
  return geminiActive ? PROVIDERS.GEMINI : PROVIDERS.OPENAI;
}

function updateMaxTokensValue() {
  const exp = clampExp(el("maxTokensRange").value);
  const tokens = expToTokens(exp);
  el("maxTokensValue").textContent = formatNumber(tokens);
  return tokens;
}

function activateTab(tab) {
  const isGemini = tab === "gemini";
  el("tabGemini").classList.toggle("tab--active", isGemini);
  el("tabOpenAI").classList.toggle("tab--active", !isGemini);

  el("tabGemini").setAttribute("aria-selected", isGemini ? "true" : "false");
  el("tabOpenAI").setAttribute("aria-selected", !isGemini ? "true" : "false");

  el("panelGemini").classList.toggle("tab-panel--active", isGemini);
  el("panelOpenAI").classList.toggle("tab-panel--active", !isGemini);
}

async function refreshUI(options = {}) {
  const { preserveKeyInputs = false, silent = false } = options;
  el("storageBackend").textContent = storageBackendName();

  const [provider, maxTok, gemModel, oaiModel, gemKey, oaiKey, gemReasoningEffort, oaiReasoningEffort] = await Promise.all([
    getDefaultProvider(),
    getMaxOutputTokens(),
    getModel(PROVIDERS.GEMINI),
    getModel(PROVIDERS.OPENAI),
    getApiKey(PROVIDERS.GEMINI),
    getApiKey(PROVIDERS.OPENAI),
    getGeminiReasoningEffort(),
    getOpenAIReasoningEffort()
  ]);

  setProviderButtons(provider);

  const exp = tokensToExp(maxTok);
  const normalizedTokens = expToTokens(exp);
  el("maxTokensRange").value = String(exp);
  el("maxTokensValue").textContent = formatNumber(normalizedTokens);
  if (normalizedTokens !== maxTok) {
    await setMaxOutputTokens(normalizedTokens);
  }

  // Models
  const geminiModelEl = el("geminiModel");
  const openaiModelEl = el("openaiModel");
  populateModelSelect(PROVIDERS.GEMINI, geminiModelEl, gemModel);
  populateModelSelect(PROVIDERS.OPENAI, openaiModelEl, oaiModel);
  const selectedGeminiModel = geminiModelEl ? geminiModelEl.value : gemModel;
  const selectedOpenAIModel = openaiModelEl ? openaiModelEl.value : oaiModel;
  const safeGeminiEffort = syncGeminiReasoningEffort(selectedGeminiModel, gemReasoningEffort);
  if (safeGeminiEffort && safeGeminiEffort !== gemReasoningEffort) {
    await setGeminiReasoningEffort(safeGeminiEffort);
  }
  const safeEffort = syncOpenAIReasoningEffort(selectedOpenAIModel, oaiReasoningEffort);
  const effectiveEffort = safeEffort || oaiReasoningEffort;
  if (safeEffort && safeEffort !== oaiReasoningEffort) {
    await setOpenAIReasoningEffort(safeEffort);
  }

  // Keys are not displayed back (security / UX). Use placeholder to indicate presence.
  if (!preserveKeyInputs) {
    el("geminiKey").value = "";
    el("openaiKey").value = "";
  }

  setKeyState(gemKey, oaiKey);

  setKeyStatus("geminiKeyStatus", gemKey);
  setKeyStatus("openaiKeyStatus", oaiKey);

  if (!silent) {
    setStatus("Configuration charge.", {
      defaultProvider: provider,
      maxOutputTokens: normalizedTokens,
      gemini: { key: gemKey ? "set" : "missing", model: gemModel, reasoningEffort: safeGeminiEffort },
      openai: { key: oaiKey ? "set" : "missing", model: oaiModel, reasoningEffort: effectiveEffort }
    });
  }
}

function buildBreakdownRow(label, tokens, cost, isSub) {
  const row = document.createElement("div");
  row.className = `breakdown__row ${isSub ? "breakdown__row--sub" : "breakdown__row--total"}`;

  const name = document.createElement("div");
  name.className = "breakdown__name";
  if (isSub) {
    const model = document.createElement("span");
    model.className = "breakdown__model";
    model.textContent = label;
    name.appendChild(model);
  } else {
    name.textContent = label;
  }

  const tokensEl = document.createElement("div");
  tokensEl.className = "breakdown__tokens";
  tokensEl.textContent = `${formatNumber(tokens)} tokens`;

  const costEl = document.createElement("div");
  costEl.className = "breakdown__cost";
  costEl.textContent = formatCost(cost);

  row.appendChild(name);
  row.appendChild(tokensEl);
  row.appendChild(costEl);
  return row;
}

function renderUsageBreakdown(breakdown) {
  const container = el("usageBreakdown");
  if (!container) return;
  container.innerHTML = "";

  const providers = [PROVIDERS.GEMINI, PROVIDERS.OPENAI];
  for (const provider of providers) {
    const data = breakdown[provider] || { tokens: 0, cost: 0, models: new Map() };
    container.appendChild(buildBreakdownRow(PROVIDER_LABELS[provider] || provider, data.tokens, data.cost, false));

    const models = Array.from(data.models.values())
      .filter((model) => (model.tokens || 0) > 0 || (model.cost || 0) > 0)
      .sort((a, b) => b.tokens - a.tokens || a.label.localeCompare(b.label));

    for (const model of models) {
      container.appendChild(buildBreakdownRow(model.label, model.tokens, model.cost, true));
    }
  }
}

async function refreshUsage() {
  const rawLogs = await getRequestLog({ fresh: true });
  const logs = rawLogs.map(normalizeUsageEntry).sort((a, b) => b.ts - a.ts);

  const breakdown = {
    gemini: { tokens: 0, cost: 0, models: new Map() },
    openai: { tokens: 0, cost: 0, models: new Map() }
  };

  const total = {
    inputTokens: 0,
    reasoningTokens: 0,
    outputTokens: 0,
    cachedInputTokens: 0,
    cost: 0,
    requests: logs.length,
    byProvider: {
      gemini: { tokens: 0, cost: 0 },
      openai: { tokens: 0, cost: 0 }
    }
  };

  for (const entry of logs) {
    const input = Math.max(0, entry.inputTokens);
    const reasoning = Math.max(0, entry.reasoningTokens);
    const output = Math.max(0, entry.outputTokens);
    const tokens = input + reasoning + output;
    const cost = estimateCost(entry);

    total.inputTokens += input;
    total.reasoningTokens += reasoning;
    total.outputTokens += output;
    total.cachedInputTokens += Math.max(0, entry.cachedInputTokens || 0);
    total.cost += cost;

    const provider = String(entry.provider || "").toLowerCase();
    const bucket = breakdown[provider];
    if (!bucket) continue;

    bucket.tokens += tokens;
    bucket.cost += cost;

    if (tokens > 0 || cost > 0) {
      const rawModel = String(entry.model || "").trim();
      const label = rawModel || UNKNOWN_MODEL_LABEL;
      const key = label.toLowerCase();
      const modelEntry = bucket.models.get(key) || { label, tokens: 0, cost: 0 };
      modelEntry.tokens += tokens;
      modelEntry.cost += cost;
      bucket.models.set(key, modelEntry);
    }
  }

  total.byProvider.gemini.tokens = breakdown.gemini.tokens;
  total.byProvider.gemini.cost = breakdown.gemini.cost;
  total.byProvider.openai.tokens = breakdown.openai.tokens;
  total.byProvider.openai.cost = breakdown.openai.cost;

  el("usageTokensTotal").textContent = formatNumber(total.inputTokens + total.reasoningTokens + total.outputTokens);
  el("usageTokensInTotal").textContent = formatNumber(total.inputTokens);
  el("usageTokensReasoningTotal").textContent = formatNumber(total.reasoningTokens);
  el("usageTokensOutTotal").textContent = formatNumber(total.outputTokens);
  el("usageCostTotal").textContent = formatCost(total.cost);
  el("usageCostGemini").textContent = formatCost(total.byProvider.gemini.cost);
  el("usageCostOpenAI").textContent = formatCost(total.byProvider.openai.cost);

  renderUsageBreakdown(breakdown);
  renderLogList(logs);
  drawUsageChart(logs);
}

function getTokenHeatClass(tokensTotal) {
  const n = Number(tokensTotal || 0);
  if (n >= 8000) return "token-heat--extreme";
  if (n >= 4000) return "token-heat--hot";
  if (n >= 1500) return "token-heat--warm";
  return "token-heat--cool";
}

function renderLogList(logs) {
  const list = el("logList");
  list.innerHTML = "";

  if (!logs.length) {
    const empty = document.createElement("div");
    empty.className = "log-item";
    empty.textContent = "Aucune activit enregistre pour le moment.";
    list.appendChild(empty);
    return;
  }

  const buildTokenFlow = (iconText, value) => {
    const wrap = document.createElement("span");
    wrap.className = "token-flow";

    const icon = document.createElement("span");
    icon.className = "token-flow__icon";
    icon.textContent = iconText;

    const val = document.createElement("span");
    val.className = "token-flow__value";
    val.textContent = formatNumber(value);

    wrap.appendChild(icon);
    wrap.appendChild(val);
    return wrap;
  };

  const slice = logs.slice(0, 40);
  for (const entry of slice) {
    const item = document.createElement("div");
    const cacheClass = entry.cache ? " log-item--cache" : "";
    item.className = `log-item ${entry.ok ? "log-item--ok" : "log-item--error"}${cacheClass}`;

    const top = document.createElement("div");
    top.className = "log-item__top";

    const title = document.createElement("div");
    title.className = "log-item__title";

    const titleText = document.createElement("span");
    titleText.className = "log-item__title-text";
    titleText.textContent = entry.fn;
    title.appendChild(titleText);

    const formulaText = String(entry.formula || "").trim();
    let detail = null;
    if (formulaText) {
      const toggle = document.createElement("button");
      toggle.className = "log-item__toggle";
      toggle.type = "button";
      toggle.setAttribute("aria-expanded", "false");
      toggle.setAttribute("aria-label", "Afficher la formule");

      const chevron = document.createElement("span");
      chevron.className = "log-item__chevron";
      toggle.appendChild(chevron);

      toggle.addEventListener("click", () => {
        const isOpen = item.classList.toggle("log-item--expanded");
        toggle.setAttribute("aria-expanded", isOpen ? "true" : "false");
        toggle.setAttribute("aria-label", isOpen ? "Masquer la formule" : "Afficher la formule");
      });

      title.appendChild(toggle);

      detail = document.createElement("div");
      detail.className = "log-item__detail";
      detail.textContent = formulaText;
    }

    const tags = document.createElement("div");
    tags.style.display = "flex";
    tags.style.gap = "6px";

    const providerTag = document.createElement("span");
    providerTag.className = "tag";
    providerTag.textContent = entry.provider ? entry.provider.toUpperCase() : "";
    tags.appendChild(providerTag);

    const statusTag = document.createElement("span");
    statusTag.className = `tag ${entry.ok ? "" : "tag--error"}`;
    statusTag.textContent = entry.ok ? "OK" : "Erreur";
    tags.appendChild(statusTag);

    if (entry.cache) {
      const cacheTag = document.createElement("span");
      cacheTag.className = "tag tag--cache";
      cacheTag.textContent = "Cache";
      tags.appendChild(cacheTag);
    }

    top.appendChild(title);
    top.appendChild(tags);

    const meta = document.createElement("div");
    meta.className = "log-item__meta";

  const tokensTotal = Math.max(0, entry.inputTokens + entry.reasoningTokens + entry.outputTokens);
    const modelLabel = entry.model ? entry.model : "";
    const durationLabel = formatDuration(entry.ms);

    const right = document.createElement("div");
    right.className = "log-item__info";
    if (!entry.ok && entry.message) {
      right.textContent = `${durationLabel}  ${entry.message.slice(0, 60)}`;
    } else {
      right.textContent = `${formatTime(entry.ts)}  ${durationLabel}${modelLabel ? `  ${modelLabel}` : ""}`;
    }

    if (entry.cache) {
      const cacheNote = document.createElement("div");
      cacheNote.className = "log-item__cache";
      cacheNote.textContent = "Cache local";
      meta.appendChild(cacheNote);
    } else {
      const tokensWrap = document.createElement("div");
      tokensWrap.className = "log-item__tokens";

      const tokensLine = document.createElement("div");
      tokensLine.className = "log-item__tokens-line";

      const heatDot = document.createElement("span");
      heatDot.className = `log-item__heat ${getTokenHeatClass(tokensTotal)}`;

      const tokensTotalEl = document.createElement("span");
      tokensTotalEl.className = "log-item__tokens-total";
      tokensTotalEl.textContent = `${formatNumber(tokensTotal)} tokens`;

      const tokensDetailEl = document.createElement("span");
      tokensDetailEl.className = "log-item__tokens-detail";
      tokensDetailEl.appendChild(document.createTextNode("("));
      tokensDetailEl.appendChild(buildTokenFlow(TOKEN_IN_ICON, entry.inputTokens));
      tokensDetailEl.appendChild(document.createTextNode(" / "));
      tokensDetailEl.appendChild(buildTokenFlow(TOKEN_REASON_ICON, entry.reasoningTokens));
      tokensDetailEl.appendChild(document.createTextNode(" / "));
      tokensDetailEl.appendChild(buildTokenFlow(TOKEN_OUT_ICON, entry.outputTokens));
      tokensDetailEl.appendChild(document.createTextNode(")"));

      tokensLine.appendChild(heatDot);
      tokensLine.appendChild(tokensTotalEl);
      tokensLine.appendChild(tokensDetailEl);
      tokensWrap.appendChild(tokensLine);
      meta.appendChild(tokensWrap);
    }

    meta.appendChild(right);

    item.appendChild(top);
    if (detail) item.appendChild(detail);
    item.appendChild(meta);
    list.appendChild(item);
  }
}

function drawUsageChart(logs) {
  const canvas = el("usageChart");
  if (!canvas) return;
  const ctx = canvas.getContext("2d");
  if (!ctx) return;

  const width = canvas.clientWidth || 320;
  const height = canvas.clientHeight || 140;
  const dpr = window.devicePixelRatio || 1;
  canvas.width = width * dpr;
  canvas.height = height * dpr;
  ctx.setTransform(dpr, 0, 0, dpr, 0, 0);

  const now = Date.now();
  const start = now - 60 * 60 * 1000;
  const buckets = new Array(60).fill(0);

  for (const entry of logs) {
    if (!entry.ts || entry.ts < start) continue;
    const index = Math.floor((entry.ts - start) / 60000);
    if (index >= 0 && index < buckets.length) {
      buckets[index] += Math.max(0, entry.inputTokens + entry.reasoningTokens + entry.outputTokens);
    }
  }

  const maxValue = Math.max(...buckets, 1);
  const styles = getComputedStyle(document.documentElement);
  const lineColor = styles.getPropertyValue("--accent-2").trim() || "#2a9d8f";
  const gridColor = "rgba(38, 70, 83, 0.12)";

  ctx.clearRect(0, 0, width, height);

  ctx.strokeStyle = gridColor;
  ctx.lineWidth = 1;
  for (let i = 1; i <= 3; i++) {
    const y = (height / 4) * i;
    ctx.beginPath();
    ctx.moveTo(0, y);
    ctx.lineTo(width, y);
    ctx.stroke();
  }

  ctx.beginPath();
  buckets.forEach((value, i) => {
    const x = (i / (buckets.length - 1)) * width;
    const y = height - (value / maxValue) * (height - 12) - 6;
    if (i === 0) ctx.moveTo(x, y);
    else ctx.lineTo(x, y);
  });

  const gradient = ctx.createLinearGradient(0, 0, 0, height);
  gradient.addColorStop(0, "rgba(42, 157, 143, 0.35)");
  gradient.addColorStop(1, "rgba(42, 157, 143, 0.02)");

  ctx.lineWidth = 2;
  ctx.strokeStyle = lineColor;
  ctx.stroke();

  ctx.lineTo(width, height);
  ctx.lineTo(0, height);
  ctx.closePath();
  ctx.fillStyle = gradient;
  ctx.fill();
}

async function refreshCacheStats() {
  const stats = await getPersistentCacheStats();
  el("cacheEntries").textContent = formatNumber(stats.entries);
  el("cacheSize").textContent = formatBytes(stats.sizeBytes);
  el("cacheGeminiEntries").textContent = formatNumber(stats.providers.gemini.entries);
  el("cacheOpenAIEntries").textContent = formatNumber(stats.providers.openai.entries);
}

async function saveCommon(options = {}) {
  const { silent = false, message = "Configuration enregistre." } = options;
  const provider = getSelectedProvider();
  const exp = clampExp(el("maxTokensRange").value);
  const maxTok = expToTokens(exp);

  const savedProvider = await setDefaultProvider(provider);
  const savedTokens = await setMaxOutputTokens(maxTok);

  if (!silent) {
    setStatus(message, { defaultProvider: savedProvider, maxOutputTokens: savedTokens });
  }

  return { savedProvider, savedTokens };
}

function initCollapsibles() {
  const toggles = document.querySelectorAll("[data-collapse-toggle]");
  toggles.forEach((btn) => {
    const targetId = btn.getAttribute("aria-controls");
    const container = btn.closest("[data-collapsible]");
    if (!targetId || !container) return;
    const body = document.getElementById(targetId);
    if (!body) return;

    const collapsed = container.classList.contains("is-collapsed");
    btn.setAttribute("aria-expanded", collapsed ? "false" : "true");

    btn.addEventListener("click", () => {
      const nextCollapsed = !container.classList.contains("is-collapsed");
      container.classList.toggle("is-collapsed", nextCollapsed);
      btn.setAttribute("aria-expanded", nextCollapsed ? "false" : "true");
    });
  });
}

function attachStorageListeners() {
  let queued = false;
  const triggerRefresh = () => {
    if (queued) return;
    queued = true;
    setTimeout(async () => {
      queued = false;
      await refreshUsage();
      await refreshCacheStats();
    }, 250);
  };

  try {
    if (typeof OfficeRuntime !== "undefined" && OfficeRuntime?.storage?.onChanged) {
      const onChanged = OfficeRuntime.storage.onChanged;
      if (typeof onChanged.add === "function") onChanged.add(() => triggerRefresh());
      else if (typeof onChanged.addListener === "function") onChanged.addListener(() => triggerRefresh());
      else if (typeof onChanged === "function") onChanged(() => triggerRefresh());
    }
  } catch {
    // ignore
  }

  try {
    window.addEventListener("storage", () => triggerRefresh());
  } catch {
    // ignore
  }
}

function closeAllHelpPopups() {
  document.querySelectorAll(HELP_SELECTOR).forEach((help) => {
    help.classList.remove("is-open");
    const btn = help.querySelector("button");
    if (btn) btn.setAttribute("aria-expanded", "false");
  });
}

function initHelpPopups() {
  document.querySelectorAll(HELP_SELECTOR).forEach((help) => {
    const btn = help.querySelector("button");
    if (!btn) return;
    btn.setAttribute("aria-expanded", "false");
    btn.addEventListener("click", (event) => {
      event.stopPropagation();
      const wasOpen = help.classList.contains("is-open");
      closeAllHelpPopups();
      const nextOpen = !wasOpen;
      help.classList.toggle("is-open", nextOpen);
      btn.setAttribute("aria-expanded", nextOpen ? "true" : "false");
    });
  });

  document.addEventListener("click", (event) => {
    if (event.target.closest(HELP_SELECTOR)) return;
    closeAllHelpPopups();
  });
}

async function persistApiKey(provider, value) {
  const trimmed = (value || "").trim();
  if (!trimmed) return "";
  await setApiKey(provider, trimmed);
  keyState[provider] = trimmed;
  updateKeyPlaceholders();
  updateKeyAlerts();
  return trimmed;
}

async function saveProviderKey(provider, inputEl, statusId) {
  const key = await persistApiKey(provider, inputEl?.value || "");
  if (!key) return;
  if (inputEl) inputEl.value = "";
  if (statusId) setKeyStatus(statusId, key);
  setStatus("Cl enregistre.", { provider });
}

async function saveProviderModel(provider) {
  const isGemini = provider === PROVIDERS.GEMINI;
  const modelEl = isGemini ? el("geminiModel") : el("openaiModel");
  if (!modelEl) return;
  const newModel = (modelEl.value || "").trim();
  await setModel(provider, newModel);

  let effort = "";
  if (isGemini) {
    const effortEl = el("geminiReasoningEffort");
    const rawEffort = effortEl ? effortEl.value : "";
    effort = syncGeminiReasoningEffort(newModel, rawEffort);
    if (effort) await setGeminiReasoningEffort(effort);
  } else {
    const effortEl = el("openaiReasoningEffort");
    const rawEffort = effortEl ? effortEl.value : "";
    effort = syncOpenAIReasoningEffort(newModel, rawEffort);
    if (effort) await setOpenAIReasoningEffort(effort);
  }

  setStatus("Configuration enregistre.", {
    provider,
    model: newModel,
    reasoningEffort: effort || undefined
  });
}

async function saveGeminiReasoningEffort() {
  const modelEl = el("geminiModel");
  const effortEl = el("geminiReasoningEffort");
  if (!effortEl) return;
  const label = syncGeminiReasoningEffort(modelEl ? modelEl.value : "", effortEl.value);
  if (label) await setGeminiReasoningEffort(label);
  setStatus("Effort de raisonnement enregistr.", { geminiReasoningEffort: label });
}

async function saveOpenAIReasoningEffort() {
  const modelEl = el("openaiModel");
  const effortEl = el("openaiReasoningEffort");
  if (!effortEl) return;
  const label = syncOpenAIReasoningEffort(modelEl ? modelEl.value : "", effortEl.value);
  if (label) await setOpenAIReasoningEffort(label);
  setStatus("Effort de raisonnement enregistr.", { openaiReasoningEffort: label });
}

async function saveOnboardingKeys() {
  const geminiInput = el("onboardingGeminiKey");
  const openaiInput = el("onboardingOpenAIKey");

  const geminiKey = await persistApiKey(PROVIDERS.GEMINI, geminiInput?.value || "");
  const openaiKey = await persistApiKey(PROVIDERS.OPENAI, openaiInput?.value || "");

  if (geminiInput) geminiInput.value = "";
  if (openaiInput) openaiInput.value = "";

  setKeyStatus("geminiKeyStatus", keyState.gemini);
  setKeyStatus("openaiKeyStatus", keyState.openai);

  if (geminiKey || openaiKey) {
    setStatus("Cls enregistres.", { gemini: !!geminiKey, openai: !!openaiKey });
  }

  if (keyState.gemini || keyState.openai) {
    setOnboardingKeysModalOpen(false);
  }
}

async function dismissOnboarding(markSeen = true) {
  if (markSeen) await setOnboardingSeen(true);
  setOnboardingKeysModalOpen(false);
  setOnboardingModalOpen(false);
}

async function maybeShowOnboarding() {
  const seen = await getOnboardingSeen();
  if (seen) return;
  setOnboardingModalOpen(true);
}

async function clearProvider(provider) {
  await clearApiKey(provider);
  await refreshUI({ silent: true });
  setStatus("Cl efface.", { provider });
}

async function testProvider(provider) {
  setStatus("Test en cours", { provider });

  const r = await aiMinimalTest(provider);
  if (r.ok) {
    setStatus("OK", { provider: r.provider, model: r.model, response: r.text });
  } else {
    setStatus("chec", { provider: r.provider, model: r.model, error: r.message, code: r.code });
  }
}

async function safeOfficeReady() {
  try {
    if (typeof Office !== "undefined" && Office?.onReady) {
      await Office.onReady();
    }
  } catch {
    // Running outside Office host (e.g., direct browser open)
  }
}

async function init() {
  initCollapsibles();
  attachStorageListeners();
  initHelpPopups();

  const pricingHelp = el("pricingHelp");
  if (pricingHelp) {
    pricingHelp.addEventListener("click", () => openPricingModal());
  }

  document.querySelectorAll("[data-modal-close]").forEach((btn) => {
    btn.addEventListener("click", () => closePricingModal());
  });

  document.querySelectorAll("[data-onboarding-close]").forEach((btn) => {
    btn.addEventListener("click", async () => dismissOnboarding(true));
  });

  document.querySelectorAll("[data-onboarding-keys-close]").forEach((btn) => {
    btn.addEventListener("click", () => setOnboardingKeysModalOpen(false));
  });

  const onboardingClose = el("onboardingClose");
  if (onboardingClose) {
    onboardingClose.addEventListener("click", async () => dismissOnboarding(true));
  }

  const onboardingDone = el("onboardingDone");
  if (onboardingDone) {
    onboardingDone.addEventListener("click", async () => dismissOnboarding(true));
  }

  const onboardingKeysButton = el("onboardingKeysButton");
  if (onboardingKeysButton) {
    onboardingKeysButton.addEventListener("click", () => setOnboardingKeysModalOpen(true));
  }

  const onboardingKeysSave = el("onboardingKeysSave");
  if (onboardingKeysSave) {
    onboardingKeysSave.addEventListener("click", async () => saveOnboardingKeys());
  }

  const onboardingKeysLater = el("onboardingKeysLater");
  if (onboardingKeysLater) {
    onboardingKeysLater.addEventListener("click", () => setOnboardingKeysModalOpen(false));
  }

  document.querySelectorAll("[data-key-alert]").forEach((alert) => {
    alert.addEventListener("click", () => setOnboardingKeysModalOpen(true));
    alert.addEventListener("keydown", (event) => {
      if (event.key !== "Enter" && event.key !== " ") return;
      event.preventDefault();
      setOnboardingKeysModalOpen(true);
    });
  });

  document.addEventListener("keydown", async (e) => {
    if (e.key !== "Escape") return;
    closeAllHelpPopups();
    if (isModalOpen("onboardingKeysModal")) {
      setOnboardingKeysModalOpen(false);
      return;
    }
    if (isModalOpen("onboardingModal")) {
      await dismissOnboarding(true);
      return;
    }
    if (isModalOpen("pricingModal")) {
      closePricingModal();
    }
  });

  // Tabs
  el("tabGemini").addEventListener("click", () => activateTab("gemini"));
  el("tabOpenAI").addEventListener("click", () => activateTab("openai"));

  // Provider selection buttons
  el("providerGemini").addEventListener("click", async () => {
    setProviderButtons(PROVIDERS.GEMINI);
    await saveCommon();
  });

  el("providerOpenAI").addEventListener("click", async () => {
    setProviderButtons(PROVIDERS.OPENAI);
    await saveCommon();
  });

  // Max tokens slider
  el("maxTokensRange").addEventListener("input", () => {
    updateMaxTokensValue();
  });

  el("maxTokensRange").addEventListener("change", async () => {
    await saveCommon();
  });

  const geminiEffortEl = el("geminiReasoningEffort");
  if (geminiEffortEl) {
    geminiEffortEl.addEventListener("input", () => {
      const modelEl = el("geminiModel");
      syncGeminiReasoningEffort(modelEl ? modelEl.value : "", geminiEffortEl.value);
    });
    geminiEffortEl.addEventListener("change", async () => {
      await saveGeminiReasoningEffort();
    });
  }

  const effortEl = el("openaiReasoningEffort");
  if (effortEl) {
    effortEl.addEventListener("input", () => {
      const allowNone = effortEl.dataset.allowNone !== "0";
      let label = getReasoningEffortLabel(effortEl.value);
      if (!allowNone && label === "none") {
        effortEl.value = "1";
        label = getReasoningEffortLabel(effortEl.value);
      }
      effortEl.setAttribute("aria-valuetext", label);
    });
    effortEl.addEventListener("change", async () => {
      await saveOpenAIReasoningEffort();
    });
  }

  const geminiModelEl = el("geminiModel");
  if (geminiModelEl) {
    geminiModelEl.addEventListener("change", async () => {
      await saveProviderModel(PROVIDERS.GEMINI);
    });
  }

  const openaiModelEl = el("openaiModel");
  if (openaiModelEl) {
    openaiModelEl.addEventListener("change", async () => {
      await saveProviderModel(PROVIDERS.OPENAI);
    });
  }

  const geminiKeyEl = el("geminiKey");
  if (geminiKeyEl) {
    geminiKeyEl.addEventListener("change", async () => {
      await saveProviderKey(PROVIDERS.GEMINI, geminiKeyEl, "geminiKeyStatus");
    });
  }

  const openaiKeyEl = el("openaiKey");
  if (openaiKeyEl) {
    openaiKeyEl.addEventListener("change", async () => {
      await saveProviderKey(PROVIDERS.OPENAI, openaiKeyEl, "openaiKeyStatus");
    });
  }

  // Provider clear/test
  el("clearGemini").addEventListener("click", async () => clearProvider(PROVIDERS.GEMINI));
  el("clearOpenAI").addEventListener("click", async () => clearProvider(PROVIDERS.OPENAI));

  el("testGemini").addEventListener("click", async () => testProvider(PROVIDERS.GEMINI));
  el("testOpenAI").addEventListener("click", async () => testProvider(PROVIDERS.OPENAI));

  el("clearLogs").addEventListener("click", async () => {
    await clearRequestLog();
    await refreshUsage();
    setStatus("Journal effac.", { ok: true });
  });

  el("refreshCache").addEventListener("click", async () => {
    await refreshCacheStats();
    setStatus("Cache actualis.", { ok: true });
  });

  el("clearCache").addEventListener("click", async () => {
    await clearAllCaches();
    await refreshCacheStats();
    setStatus("Cache vid.", { ok: true });
  });

  // Default tab
  activateTab("gemini");

  await refreshUI();
  await refreshUsage();
  await refreshCacheStats();
  await maybeShowOnboarding();

  setInterval(refreshUsage, 2000);
  setInterval(refreshCacheStats, 12000);
}

document.addEventListener("DOMContentLoaded", async () => {
  await safeOfficeReady();
  await init();
});
