// src/shared/core.js
// Shared utilities + persisted configuration for the AI.Native add-in.
//
// Key design goals:
// - Two providers (Gemini + OpenAI) can coexist.
// - The default provider is chosen in the taskpane (not in formulas).
// - No temperature / sampling controls anywhere (per requirements).
// - A single "max output tokens" setting applies to both providers.

export const PROVIDERS = Object.freeze({
  GEMINI: "gemini",
  OPENAI: "openai"
});

export const DEFAULTS = Object.freeze({
  provider: PROVIDERS.GEMINI,
  // Default models (User specified future versions)
  geminiModel: "gemini-3-flash-preview",
  openaiModel: "gpt-5-mini",
  geminiReasoningEffort: "auto",
  openaiReasoningEffort: "medium",
  maxOutputTokens: 4096,

  // Execution / UX
  retry: 2,
  timeoutMs: 120000,

  // In-memory cache (per runtime). Note: Office custom functions can run in a long-lived runtime.
  cache: true,
  cacheTtlSec: 7 * 24 * 3600, // 7 days
  webCacheTtlSec: 3600 // 1 hour (web results change more often)
});

const STORAGE_KEYS = Object.freeze({
  DEFAULT_PROVIDER: "AI_DEFAULT_PROVIDER_V3",
  GEMINI_API_KEY: "AI_GEMINI_API_KEY_V3",
  OPENAI_API_KEY: "AI_OPENAI_API_KEY_V3",
  GEMINI_MODEL: "AI_GEMINI_MODEL_V3",
  OPENAI_MODEL: "AI_OPENAI_MODEL_V3",
  GEMINI_REASONING_EFFORT: "AI_GEMINI_REASONING_EFFORT_V3",
  OPENAI_REASONING_EFFORT: "AI_OPENAI_REASONING_EFFORT_V3",
  MAX_OUTPUT_TOKENS: "AI_MAX_OUTPUT_TOKENS_V3",
  ONBOARDING_SEEN: "AI_ONBOARDING_SEEN_V1",

  // Prevent re-running migrations each time.
  MIGRATION_DONE: "AI_MIGRATION_DONE_V3",

  // Diagnostics + cache metadata
  REQUEST_LOG: "AI_REQUEST_LOG_V1",
  REQUEST_LOG_CLEAR_AT: "AI_REQUEST_LOG_CLEAR_AT_V1",
  CACHE_INDEX: "AI_CACHE_INDEX_V1",
  CACHE_CLEAR_AT: "AI_CACHE_CLEAR_AT_V1"
});

// Older keys used by previous versions (best-effort migration).
const LEGACY_KEYS = Object.freeze({
  GEMINI_API_KEY: "GEMINI_API_KEY",
  MAX_OUTPUT_TOKENS: "MAX_OUTPUT_TOKENS",

  DEFAULT_PROVIDER_V2: "AI_DEFAULT_PROVIDER_V2",
  GEMINI_API_KEY_V2: "AI_GEMINI_API_KEY_V2",
  OPENAI_API_KEY_V2: "AI_OPENAI_API_KEY_V2",
  GEMINI_MODEL_V2: "AI_GEMINI_MODEL_V2",
  OPENAI_MODEL_V2: "AI_OPENAI_MODEL_V2",

  GEMINI_MAX_TOKENS_V2: "AI_GEMINI_MAX_OUTPUT_TOKENS_V2",
  OPENAI_MAX_TOKENS_V2: "AI_OPENAI_MAX_OUTPUT_TOKENS_V2"
});

let _migrationPromise = null;

function getRuntimeStorage() {
  try {
    if (typeof OfficeRuntime !== "undefined" && OfficeRuntime?.storage?.getItem) {
      return { kind: "office", storage: OfficeRuntime.storage };
    }
  } catch {
    // ignore
  }

  try {
    if (typeof window !== "undefined" && window?.localStorage?.getItem) {
      return { kind: "local", storage: window.localStorage };
    }
  } catch {
    // ignore
  }

  // Extremely defensive fallback (in-memory only).
  const mem = new Map();
  return {
    kind: "memory",
    storage: {
      async getItem(k) {
        return mem.has(k) ? mem.get(k) : null;
      },
      async setItem(k, v) {
        mem.set(k, String(v));
      },
      async removeItem(k) {
        mem.delete(k);
      }
    }
  };
}

function getLocalStorageSafe() {
  try {
    if (typeof window !== "undefined" && window?.localStorage?.getItem) {
      return window.localStorage;
    }
  } catch {
    return null;
  }
  return null;
}

export function storageBackendName() {
  return getRuntimeStorage().kind;
}

async function rawGet(key) {
  const { kind, storage } = getRuntimeStorage();
  if (kind === "local") {
    try {
      return storage.getItem(key);
    } catch {
      return null;
    }
  }

  if (kind === "office") {
    try {
      const result = await storage.getItem(key);
      if (result == null) {
        const local = getLocalStorageSafe();
        if (!local) return null;
        try {
          const localValue = local.getItem(key);
          return localValue == null ? null : localValue;
        } catch {
          return null;
        }
      }
      return result;
    } catch {
      const local = getLocalStorageSafe();
      if (!local) return null;
      try {
        return local.getItem(key);
      } catch {
        return null;
      }
    }
  }

  try {
    return await storage.getItem(key);
  } catch {
    return null;
  }
}

async function rawSet(key, value) {
  const { kind, storage } = getRuntimeStorage();
  const v = value == null ? "" : String(value);
  if (kind === "local") {
    try {
      storage.setItem(key, v);
      return;
    } catch {
      return;
    }
  }

  if (kind === "office") {
    try {
      await storage.setItem(key, v);
    } catch {
      // ignore and try local
    }
    const local = getLocalStorageSafe();
    if (!local) return;
    try {
      local.setItem(key, v);
    } catch {
      // ignore
    }
    return;
  }

  try {
    await storage.setItem(key, v);
  } catch {
    // ignore
  }
}

async function rawRemove(key) {
  const { kind, storage } = getRuntimeStorage();
  if (kind === "local") {
    try {
      storage.removeItem(key);
      return;
    } catch {
      return;
    }
  }

  if (kind === "office") {
    try {
      await storage.removeItem(key);
    } catch {
      // ignore and try local
    }
    const local = getLocalStorageSafe();
    if (!local) return;
    try {
      local.removeItem(key);
    } catch {
      // ignore
    }
    return;
  }

  try {
    await storage.removeItem(key);
  } catch {
    // ignore
  }
}

async function ensureMigrated() {
  if (_migrationPromise) return _migrationPromise;

  _migrationPromise = (async () => {
    // Fast exit if already migrated
    const done = await rawGet(STORAGE_KEYS.MIGRATION_DONE);
    if (done === "1") return;

    // Migrate provider
    const currentProvider = normalizeProvider(await rawGet(STORAGE_KEYS.DEFAULT_PROVIDER));
    if (!currentProvider) {
      const legacyProvider = normalizeProvider(await rawGet(LEGACY_KEYS.DEFAULT_PROVIDER_V2));
      await rawSet(STORAGE_KEYS.DEFAULT_PROVIDER, legacyProvider || DEFAULTS.provider);
    }

    // Migrate keys
    const gKey = await rawGet(STORAGE_KEYS.GEMINI_API_KEY);
    if (!gKey) {
      const legacy = (await rawGet(LEGACY_KEYS.GEMINI_API_KEY_V2)) || (await rawGet(LEGACY_KEYS.GEMINI_API_KEY));
      if (legacy) await rawSet(STORAGE_KEYS.GEMINI_API_KEY, legacy);
    }

    const oKey = await rawGet(STORAGE_KEYS.OPENAI_API_KEY);
    if (!oKey) {
      const legacy = await rawGet(LEGACY_KEYS.OPENAI_API_KEY_V2);
      if (legacy) await rawSet(STORAGE_KEYS.OPENAI_API_KEY, legacy);
    }

    // Migrate models
    const gModel = await rawGet(STORAGE_KEYS.GEMINI_MODEL);
    if (!gModel) {
      const legacy = await rawGet(LEGACY_KEYS.GEMINI_MODEL_V2);
      await rawSet(STORAGE_KEYS.GEMINI_MODEL, legacy || DEFAULTS.geminiModel);
    }

    const oModel = await rawGet(STORAGE_KEYS.OPENAI_MODEL);
    if (!oModel) {
      const legacy = await rawGet(LEGACY_KEYS.OPENAI_MODEL_V2);
      await rawSet(STORAGE_KEYS.OPENAI_MODEL, legacy || DEFAULTS.openaiModel);
    }

    // Migrate max tokens (unified)
    const tok = await rawGet(STORAGE_KEYS.MAX_OUTPUT_TOKENS);
    if (!tok) {
      const legacyUnified = await rawGet(LEGACY_KEYS.MAX_OUTPUT_TOKENS);
      const legacyGem = await rawGet(LEGACY_KEYS.GEMINI_MAX_TOKENS_V2);
      const legacyOai = await rawGet(LEGACY_KEYS.OPENAI_MAX_TOKENS_V2);

      const candidates = [legacyUnified, legacyGem, legacyOai]
        .map((x) => parseInt(String(x || "").trim(), 10))
        .filter((n) => Number.isFinite(n) && n > 0);

      const migrated = candidates.length ? Math.max(...candidates) : DEFAULTS.maxOutputTokens;
      await rawSet(STORAGE_KEYS.MAX_OUTPUT_TOKENS, String(migrated));
    }

    await rawSet(STORAGE_KEYS.MIGRATION_DONE, "1");
  })();

  try {
    await _migrationPromise;
  } finally {
    _migrationPromise = null;
  }
}

export function normalizeProvider(p) {
  if (!p) return "";
  const s = String(p).trim().toLowerCase();
  if (s === PROVIDERS.GEMINI) return PROVIDERS.GEMINI;
  if (s === PROVIDERS.OPENAI) return PROVIDERS.OPENAI;
  // Common aliases (defensive)
  if (s === "google" || s === "gem") return PROVIDERS.GEMINI;
  if (s === "oai" || s === "chatgpt") return PROVIDERS.OPENAI;
  return "";
}

const GEMINI_REASONING_EFFORTS = new Set(["auto", "minimal", "low", "medium", "high"]);
const OPENAI_REASONING_EFFORTS = new Set(["none", "minimal", "low", "medium", "high"]);

function normalizeGeminiReasoningEffort(value) {
  const v = String(value || "").trim().toLowerCase();
  if (!v) return DEFAULTS.geminiReasoningEffort;
  return GEMINI_REASONING_EFFORTS.has(v) ? v : DEFAULTS.geminiReasoningEffort;
}

function normalizeReasoningEffort(value) {
  const v = String(value || "").trim().toLowerCase();
  if (!v) return DEFAULTS.openaiReasoningEffort;
  return OPENAI_REASONING_EFFORTS.has(v) ? v : DEFAULTS.openaiReasoningEffort;
}

export async function getDefaultProvider() {
  await ensureMigrated();
  const p = normalizeProvider(await rawGet(STORAGE_KEYS.DEFAULT_PROVIDER));
  return p || DEFAULTS.provider;
}

export async function setDefaultProvider(provider) {
  const p = normalizeProvider(provider) || DEFAULTS.provider;
  await rawSet(STORAGE_KEYS.DEFAULT_PROVIDER, p);
  return p;
}

function keyKey(provider) {
  const p = normalizeProvider(provider);
  if (p === PROVIDERS.OPENAI) return STORAGE_KEYS.OPENAI_API_KEY;
  return STORAGE_KEYS.GEMINI_API_KEY;
}

export async function getApiKey(provider) {
  await ensureMigrated();
  const k = await rawGet(keyKey(provider));
  return (k || "").trim();
}

export async function setApiKey(provider, apiKey) {
  const k = (apiKey || "").trim();
  await rawSet(keyKey(provider), k);
  return k;
}

export async function clearApiKey(provider) {
  await rawRemove(keyKey(provider));
}

function modelKey(provider) {
  const p = normalizeProvider(provider);
  if (p === PROVIDERS.OPENAI) return STORAGE_KEYS.OPENAI_MODEL;
  return STORAGE_KEYS.GEMINI_MODEL;
}

export async function getModel(provider) {
  await ensureMigrated();
  const p = normalizeProvider(provider);
  const raw = await rawGet(modelKey(p));
  if (raw && String(raw).trim()) return String(raw).trim();
  return p === PROVIDERS.OPENAI ? DEFAULTS.openaiModel : DEFAULTS.geminiModel;
}

export async function getGeminiReasoningEffort() {
  await ensureMigrated();
  const raw = await rawGet(STORAGE_KEYS.GEMINI_REASONING_EFFORT);
  return normalizeGeminiReasoningEffort(raw || DEFAULTS.geminiReasoningEffort);
}

export async function getOpenAIReasoningEffort() {
  await ensureMigrated();
  const raw = await rawGet(STORAGE_KEYS.OPENAI_REASONING_EFFORT);
  return normalizeReasoningEffort(raw || DEFAULTS.openaiReasoningEffort);
}

export async function setModel(provider, model) {
  const p = normalizeProvider(provider);
  const v = String(model || "").trim();
  if (!v) {
    // Reset to default if emptied
    await rawSet(modelKey(p), p === PROVIDERS.OPENAI ? DEFAULTS.openaiModel : DEFAULTS.geminiModel);
    return;
  }
  await rawSet(modelKey(p), v);
}

export async function setOpenAIReasoningEffort(value) {
  const v = normalizeReasoningEffort(value);
  await rawSet(STORAGE_KEYS.OPENAI_REASONING_EFFORT, v);
  return v;
}

export async function setGeminiReasoningEffort(value) {
  const v = normalizeGeminiReasoningEffort(value);
  await rawSet(STORAGE_KEYS.GEMINI_REASONING_EFFORT, v);
  return v;
}

export async function getMaxOutputTokens() {
  await ensureMigrated();
  const raw = await rawGet(STORAGE_KEYS.MAX_OUTPUT_TOKENS);
  const n = parseInt(String(raw || "").trim(), 10);
  return clampInt(n, 1, 128000, DEFAULTS.maxOutputTokens);
}

export async function setMaxOutputTokens(value) {
  const n = clampInt(value, 1, 128000, DEFAULTS.maxOutputTokens);
  await rawSet(STORAGE_KEYS.MAX_OUTPUT_TOKENS, String(n));
  return n;
}

export async function getOnboardingSeen() {
  await ensureMigrated();
  const raw = await rawGet(STORAGE_KEYS.ONBOARDING_SEEN);
  return raw === "1";
}

export async function setOnboardingSeen(seen = true) {
  await rawSet(STORAGE_KEYS.ONBOARDING_SEEN, seen ? "1" : "0");
  return seen;
}

// ---------- Small utilities ----------
export function nowMs() {
  return Date.now();
}

export function sleep(ms) {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

export function clamp(value, min, max, fallback = min) {
  const n = Number(value);
  if (!Number.isFinite(n)) return fallback;
  return Math.max(min, Math.min(max, n));
}

export function clampInt(value, min, max, fallback = min) {
  const n = parseInt(String(value), 10);
  if (!Number.isFinite(n)) return fallback;
  return Math.max(min, Math.min(max, n));
}

function parseTimestampMs(raw) {
  const n = parseInt(String(raw || "").trim(), 10);
  return Number.isFinite(n) && n > 0 ? n : 0;
}

// ---------- LRU cache with TTL ----------
export class LRUCache {
  constructor(maxEntries = 500) {
    this.maxEntries = maxEntries;
    this.map = new Map(); // key => {value, expiresAtMs}
  }

  get(key) {
    if (!this.map.has(key)) return null;
    const entry = this.map.get(key);

    if (entry?.expiresAtMs && entry.expiresAtMs <= nowMs()) {
      this.map.delete(key);
      return null;
    }

    // Refresh key order (most recent)
    this.map.delete(key);
    this.map.set(key, entry);
    return entry.value;
  }

  set(key, value, ttlMs) {
    const expiresAtMs = typeof ttlMs === "number" && ttlMs > 0 ? nowMs() + ttlMs : null;
    if (this.map.has(key)) this.map.delete(key);
    this.map.set(key, { value, expiresAtMs });

    // Evict LRU
    while (this.map.size > this.maxEntries) {
      const oldestKey = this.map.keys().next().value;
      this.map.delete(oldestKey);
    }
  }

  clear() {
    this.map.clear();
  }
}

// ---------- Persistent cache (survives reloads) ----------
const CACHE_ENTRY_PREFIX = "AI_CACHE_ENTRY_V1:";
const CACHE_INDEX_MAX = 250;
let _cacheClearMs = 0;

async function syncCacheClearMarker() {
  const clearedAt = parseTimestampMs(await rawGet(STORAGE_KEYS.CACHE_CLEAR_AT));
  if (clearedAt > _cacheClearMs) _cacheClearMs = clearedAt;
  return _cacheClearMs;
}

export async function getCacheClearAt() {
  return await syncCacheClearMarker();
}

async function loadCacheIndex() {
  const raw = await rawGet(STORAGE_KEYS.CACHE_INDEX);
  if (!raw) return [];
  try {
    const arr = JSON.parse(raw);
    return Array.isArray(arr) ? arr : [];
  } catch {
    return [];
  }
}

async function saveCacheIndex(entries) {
  try {
    await rawSet(STORAGE_KEYS.CACHE_INDEX, JSON.stringify(entries));
  } catch {
    // ignore
  }
}

function pruneCacheIndex(entries, clearedAt = 0) {
  const now = nowMs();
  const kept = [];
  const removedKeys = [];

  for (const entry of entries || []) {
    if (!entry || !entry.key) continue;
    const savedAt = Number(entry.savedAtMs || 0);
    if (clearedAt && (!Number.isFinite(savedAt) || savedAt <= clearedAt)) {
      removedKeys.push(entry.key);
      continue;
    }
    if (entry.expiresAtMs && entry.expiresAtMs <= now) {
      removedKeys.push(entry.key);
      continue;
    }
    kept.push(entry);
  }

  if (kept.length > CACHE_INDEX_MAX) {
    kept.sort((a, b) => (a.savedAtMs || 0) - (b.savedAtMs || 0));
    const over = kept.length - CACHE_INDEX_MAX;
    const toRemove = kept.slice(0, over);
    for (const r of toRemove) removedKeys.push(r.key);
    return { list: kept.slice(over), removedKeys };
  }

  return { list: kept, removedKeys };
}

async function removeCacheEntry(key) {
  if (!key) return;
  await rawRemove(CACHE_ENTRY_PREFIX + key);
}

async function removeFromCacheIndex(key) {
  const index = await loadCacheIndex();
  const next = (index || []).filter((item) => item?.key !== key);
  if (next.length !== index.length) await saveCacheIndex(next);
}

export async function getPersistentCache(cacheKey) {
  if (!cacheKey) return null;
  const clearedAt = await syncCacheClearMarker();
  const raw = await rawGet(CACHE_ENTRY_PREFIX + cacheKey);
  if (!raw) {
    await removeFromCacheIndex(cacheKey);
    return null;
  }

  try {
    const entry = JSON.parse(raw);
    if (!entry || typeof entry !== "object") throw new Error("Invalid entry");
    const savedAt = Number(entry.savedAtMs || 0);
    if (clearedAt && (!Number.isFinite(savedAt) || savedAt <= clearedAt)) {
      await removeCacheEntry(cacheKey);
      await removeFromCacheIndex(cacheKey);
      return null;
    }
    if (entry.expiresAtMs && entry.expiresAtMs <= nowMs()) {
      await removeCacheEntry(cacheKey);
      await removeFromCacheIndex(cacheKey);
      return null;
    }
    return entry.value || null;
  } catch {
    await removeCacheEntry(cacheKey);
    await removeFromCacheIndex(cacheKey);
    return null;
  }
}

export async function setPersistentCache(cacheKey, value, ttlMs, meta = {}) {
  if (!cacheKey) return;
  const now = nowMs();
  const clearedAt = await syncCacheClearMarker();
  const savedAtMs = clearedAt && now <= clearedAt ? clearedAt + 1 : now;
  const expiresAtMs = typeof ttlMs === "number" && ttlMs > 0 ? savedAtMs + ttlMs : null;
  const entry = {
    v: 1,
    savedAtMs,
    expiresAtMs,
    value
  };

  const raw = JSON.stringify(entry);
  await rawSet(CACHE_ENTRY_PREFIX + cacheKey, raw);

  const index = await loadCacheIndex();
  const provider = meta.provider || value?.provider || "";
  const model = meta.model || value?.model || "";
  const size = raw.length;

  let updated = false;
  const next = (index || []).map((item) => {
    if (item?.key !== cacheKey) return item;
    updated = true;
    return {
      key: cacheKey,
      provider,
      model,
      size,
      savedAtMs,
      expiresAtMs
    };
  });

  if (!updated) {
    next.push({
      key: cacheKey,
      provider,
      model,
      size,
      savedAtMs,
      expiresAtMs
    });
  }

  const pruned = pruneCacheIndex(next, clearedAt);
  for (const key of pruned.removedKeys) await removeCacheEntry(key);
  await saveCacheIndex(pruned.list);
}

export async function clearPersistentCache() {
  const clearedAt = nowMs();
  _cacheClearMs = clearedAt;
  await rawSet(STORAGE_KEYS.CACHE_CLEAR_AT, String(clearedAt));
  const index = await loadCacheIndex();
  for (const item of index || []) {
    if (!item?.key) continue;
    await removeCacheEntry(item.key);
  }
  await rawRemove(STORAGE_KEYS.CACHE_INDEX);
}

export async function getPersistentCacheStats() {
  const index = await loadCacheIndex();
  const clearedAt = await syncCacheClearMarker();
  const pruned = pruneCacheIndex(index, clearedAt);
  if (pruned.removedKeys.length) {
    for (const key of pruned.removedKeys) await removeCacheEntry(key);
    await saveCacheIndex(pruned.list);
  }

  const stats = {
    entries: 0,
    sizeBytes: 0,
    providers: {
      gemini: { entries: 0, sizeBytes: 0 },
      openai: { entries: 0, sizeBytes: 0 }
    },
    oldestMs: null,
    newestMs: null
  };

  for (const item of pruned.list || []) {
    if (!item?.key) continue;
    stats.entries += 1;
    stats.sizeBytes += Number(item.size || 0);
    const provider = String(item.provider || "").toLowerCase();
    if (provider === PROVIDERS.GEMINI) {
      stats.providers.gemini.entries += 1;
      stats.providers.gemini.sizeBytes += Number(item.size || 0);
    } else if (provider === PROVIDERS.OPENAI) {
      stats.providers.openai.entries += 1;
      stats.providers.openai.sizeBytes += Number(item.size || 0);
    }

    const savedAt = Number(item.savedAtMs || 0);
    if (savedAt) {
      stats.oldestMs = stats.oldestMs == null ? savedAt : Math.min(stats.oldestMs, savedAt);
      stats.newestMs = stats.newestMs == null ? savedAt : Math.max(stats.newestMs, savedAt);
    }
  }

  return stats;
}

// ---------- Stable stringify + hashing (for cache keys) ----------
function isPlainObject(v) {
  return Object.prototype.toString.call(v) === "[object Object]";
}

export function stableStringify(value) {
  return JSON.stringify(sortKeysDeep(value));
}

function sortKeysDeep(value) {
  if (Array.isArray(value)) return value.map(sortKeysDeep);
  if (isPlainObject(value)) {
    const out = {};
    for (const k of Object.keys(value).sort()) out[k] = sortKeysDeep(value[k]);
    return out;
  }
  return value;
}

function fnv1a32(str) {
  let h = 2166136261;
  for (let i = 0; i < str.length; i++) {
    h ^= str.charCodeAt(i);
    h = Math.imul(h, 16777619);
  }
  // Unsigned
  return (h >>> 0).toString(16).padStart(8, "0");
}

export async function hashKey(input) {
  const text = typeof input === "string" ? input : stableStringify(input);

  // Prefer SHA-256 when available (browser/Office runtime)
  try {
    if (typeof crypto !== "undefined" && crypto?.subtle?.digest && typeof TextEncoder !== "undefined") {
      const enc = new TextEncoder();
      const bytes = enc.encode(text);
      const digest = await crypto.subtle.digest("SHA-256", bytes);
      const arr = Array.from(new Uint8Array(digest));
      return arr.map((b) => b.toString(16).padStart(2, "0")).join("");
    }
  } catch {
    // ignore and fallback
  }

  return `fnv1a:${fnv1a32(text)}`;
}

// ---------- Lightweight diagnostics log (persisted) ----------
const REQUEST_LOG_MAX = 500;
const REQUEST_LOG_TTL_MS = 24 * 60 * 60 * 1000; // 24h
let _requestLog = [];
let _requestLogLoading = null;
let _requestLogLoaded = false;
let _requestLogLastLoadedMs = 0;
let _requestLogClearMs = 0;

async function syncRequestLogClearMarker(options = {}) {
  const clearedAt = parseTimestampMs(await rawGet(STORAGE_KEYS.REQUEST_LOG_CLEAR_AT));
  if (clearedAt > _requestLogClearMs) {
    _requestLogClearMs = clearedAt;
    _requestLog = [];
    if (options.reset) {
      _requestLogLoaded = true;
      _requestLogLoading = null;
      _requestLogLastLoadedMs = nowMs();
    } else {
      _requestLogLoaded = false;
    }
  }
  return _requestLogClearMs;
}

async function loadRequestLog(force = false) {
  if (_requestLogLoading) return _requestLogLoading;

  _requestLogLoading = (async () => {
    const clearedAt = await syncRequestLogClearMarker();
    if (!force && _requestLogLoaded) return _requestLog;
    const raw = await rawGet(STORAGE_KEYS.REQUEST_LOG);
    let arr = [];
    if (raw) {
      try {
        const parsed = JSON.parse(raw);
        if (Array.isArray(parsed)) arr = parsed;
      } catch {
        arr = [];
      }
    }
    const now = nowMs();
    arr = arr
      .map((e) => {
        if (!e) return null;
        let ts = e.ts;
        if (typeof ts === "string") {
          const parsed = Date.parse(ts);
          if (Number.isFinite(parsed)) ts = parsed;
        }
        if (typeof ts !== "number") return null;
        return { ...e, ts };
      })
      .filter((e) => e && typeof e.ts === "number" && e.ts > clearedAt && now - e.ts <= REQUEST_LOG_TTL_MS);
    _requestLog = arr.slice(-REQUEST_LOG_MAX);
    _requestLogLoaded = true;
    _requestLogLastLoadedMs = nowMs();
    return _requestLog;
  })();

  try {
    return await _requestLogLoading;
  } finally {
    _requestLogLoading = null;
  }
}

async function persistRequestLog() {
  try {
    await rawSet(STORAGE_KEYS.REQUEST_LOG, JSON.stringify(_requestLog.slice(-REQUEST_LOG_MAX)));
  } catch {
    // ignore
  }
}

function pruneRequestLog() {
  const now = nowMs();
  const clearedAt = _requestLogClearMs || 0;
  _requestLog = (_requestLog || []).filter(
    (e) => e && typeof e.ts === "number" && e.ts > clearedAt && now - e.ts <= REQUEST_LOG_TTL_MS
  );
  if (_requestLog.length > REQUEST_LOG_MAX) {
    _requestLog = _requestLog.slice(-REQUEST_LOG_MAX);
  }
}

export function logRequest(entry) {
  void logRequestAsync(entry);
}

async function logRequestAsync(entry) {
  try {
    await syncRequestLogClearMarker({ reset: true });
    const logEntry = { ts: nowMs(), ...entry };

    if (_requestLogLoaded) {
      _requestLog.push(logEntry);
      pruneRequestLog();
      _requestLogLastLoadedMs = nowMs();
      await persistRequestLog();
      return;
    }

    await loadRequestLog();
    _requestLog.push(logEntry);
    pruneRequestLog();
    _requestLogLastLoadedMs = nowMs();
    await persistRequestLog();
  } catch {
    // ignore
  }
}

export async function getRequestLog(options = {}) {
  const force = !!options?.fresh;
  const now = nowMs();
  if (force || !_requestLogLoaded || now - _requestLogLastLoadedMs > 1500) {
    await loadRequestLog(true);
  } else {
    await loadRequestLog(false);
  }
  return _requestLog.slice();
}

export async function clearRequestLog() {
  const clearedAt = nowMs();
  _requestLog = [];
  _requestLogLoaded = true;
  _requestLogLoading = null;
  _requestLogClearMs = clearedAt;
  _requestLogLastLoadedMs = clearedAt;
  await rawSet(STORAGE_KEYS.REQUEST_LOG_CLEAR_AT, String(clearedAt));
  await rawRemove(STORAGE_KEYS.REQUEST_LOG);
}
